import React, { useState, useEffect } from "react";
import {
  Free,
  Buzy,
  Next,
  ViewBooking,
  AddBooking,
} from "../components/RoomDisplay";
import {
  getRoom,
  getRoomBookingsForToday,
  addBooking,
  deleteBooking,
} from "../logic/intranets-api";

import { useTimer } from "react-timer-hook";
import { getCurrentBookingStatus } from "../logic/bookings";
import moment from "moment-timezone"
const RoomDisplay = (props) => {
  const [room, setRoom] = useState({});
  const [refresh, setRefresh] = useState(0);
  const {
    seconds,
    minutes,
    hours,
    days,
    isRunning,
    start,
    pause,
    resume,
    restart,
  } = useTimer({
    autoStart: true,
    expiryTimestamp: Date.now() + 5000,
    onExpire: () => console.warn("onExpire called"),
  });

  useEffect(
    () => {
      async function run() {
        getRoomBookingsForToday(props.email).then((data) =>
          setRoomBookings(data.value)
        );
        getRoom(props.email).then((room) => setRoom(room));
      }
      run();
    },
    [props.email],
    refresh
  );

  const [roomBookings, setRoomBookings] = useState([]);
  var [isAvailable, availableFrom,availableTo,currentBooking,nextBooking] = getCurrentBookingStatus(roomBookings,moment())
  
  /**
   * <span>{days}</span>:<span>{hours}</span>:<span>{minutes}</span>:<span>{seconds}</span>
   */
  return (
    <div>
      Room {room.title} - 
      <div style={{ fontSize: "100px",textAlign:"center",  backgroundColor:isAvailable ? "green":"red" }}>
      
      {isAvailable ? "available":"buzy"}
      </div>
      <div style={{ padding: "16px" }}>
        <AddBooking
          onAdd={async (startDateTime, endDateTime) => {
            startDateTime = moment().format("YYYY-MM-DDTHH:mm:00")
            
            endDateTime = moment().add(15,"m").format("YYYY-MM-DDTHH:mm:00")
            await addBooking(props.room.email, startDateTime, endDateTime);
            window.location.reload();
            //setRefresh(refresh + 1);
          }}
        ></AddBooking>
      </div>
      {roomBookings.map((booking) => {
        return (
          <ViewBooking
            onDelete={async (id) => {
              await deleteBooking(props.room.email, id);
              window.location.reload();
            }}
            {...booking}
          />
        );
      })}
    </div>
  );
};

export default RoomDisplay;
