import React, { useState } from 'react';
import "./display.css"
import moment from "moment"
import momenttz from "moment-timezone"
import TimePicker from 'react-time-picker';

export const Buzy = (props) => {
    return (  <div className="buzy-container">{props.title}
    <div className="buzy-container-status-red">

    </div>
    </div>);
}

export const Free = () => {
    return (  <div>Free</div>);
}

export const Next = () => {
    return (  <div>Next</div>);
}

export const AddBooking = (props) => {
    const [value, onChange] = useState('10:00');
    /**
     *  <TimePicker
        onChange={onChange}
        value={value}
      />
     */
    return (  <div>
       
        <button onClick={()=>{
            if (props.onAdd){
                props.onAdd()
            }
        }}>Add Booking</button>
        </div>);
}
 
 

export const ViewBooking = (props) => {
    return (  <div style={{padding:"10px"}}>
         <div>Organizer: {props.organizer.emailAddress.name} ({props.organizer.emailAddress.address})</div>
        <div>{momenttz(props.start.dateTime).tz("Europe/Paris").format("hh:mm")} - {momenttz(props.end.dateTime).tz("Europe/Paris").format("hh:mm")}</div>
        <button onClick={()=>{
            if (props.onDelete){
                props.onDelete(props.id)
            }
        }}>Delete Booking</button>
        </div>); 
}

