  export function addBooking(email,startDateTime,endDateTime) {
    
    var data = {startDateTime,endDateTime,"timeZone":"UTC"}

    var config = {
      method: 'post',
      url: 'https://nets-intranets-api.azurewebsites.net/v1.0/book/'+email,
      headers: { 
        'Content-Type': 'application/json'
      },
      data : data
    };

    return callAxios(config)
    }
    
    export function deleteBooking(email,id) {
    
    
      var config = {
        method: 'delete',
        url: 'https://nets-intranets-api.azurewebsites.net/v1.0/booking/'+email + "/" + id,
        headers: { 
          'Content-Type': 'application/json'
        },
        
      };
  
      return callAxios(config)
      }
      